		fn(){
			var timer = setTimeout(() => {
					//代码
					var temArr = []
					var sum = 0
					for(let i=0 ; i<this.arr.length;i++){
						sum = parseInt(this.arr[i] + sum)
						temArr.push({
							index:i+1,
							top:sum
						})
					}
					temArr.unshift({index:0,top:0})
					temArr.pop()
					this.getTop = temArr
					clearTimeout(timer)
				}, 500);  
		}
		
	},
